
/*
    //container pater
    flex-wrap: wrap; coloca los elementos debajo si no hay espacio al lado
    flex-wrap: no-wrap; coloca los elementos en fila/columna sin respeta el width
    flex-flow: row wrap ; recibe el valor de direction y wrap 2 por 1
    justify-content: flex-start/flex-end/center/space-around(espacio entre elementos);
    align-items: center; 
    align-content: center alinear grupos 
    
    // container childs
    flex-grow proporcion que crece este elemento conrespecto de los demas;
    flex-shrink proporcion que decrece este elemento conrespecto de los demas;
    flex-grow: 1;
    flex-shrink: 2;
    flex-basis:500px;
    flex replaces the 3 lines
    flex: 1 2 500px;
    order: 1; 

*/
